/**
 *  
 */

export default {
    data () {
        return {
            loadingText: '',
            list: [],                                           // 
            searchData: {                                       // 
                page: this.$store.state.page,              // 
                limit: this.$store.state.limit            // 
            },
            totalElement: this.$store.state.totalElement        // 
        };
    },
    computed: {
        // 
        pageShow () {
            return !!this.totalElement;
        }
    },
    created () {
        this.currentChange(1);
    },
    methods: {
        currentChange (val) {
            this.loading = true;
            this.searchData.page = val;
            // 
            this._getList();
        },
        search () {
            this.currentChange(1);
        }
    }
};



