                    <span>{{ item.cardReceiptSum }}</span>
                  </el-form-item>
                  <el-form-item v-for="item, index in props.row.referrerAccountCollectDtos" :key="index + 'referrerreferrer'" :label="`${item.referrerName}`">
                    <span>{{ item.referrerReceiptSum }}</span>
