            </el-table-column>
            <el-table-column
              prop="referrerReceiptSum"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column label="" align="center" width="200px" fixed="right">
              <template slot-scope="scope">
                <a @click="clickUpdate(scope.row, 1)"></a>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <hy-page v-show="pageShow"
                   :current-page="searchData.page"
                   :page-size="searchData.limit"
                   :total-element="totalElement"
                   @current-change="currentChange"></hy-page>
        </div>
      </TabPane>
      <TabPane label="" name="two">
        <div class="lk-search-bar">
          <Form label-position="right" inline :label-width="60">
            <FormItem label="">
              <DatePicker v-model="searchDate" type="date" placeholder="Select date" style="width: 200px"></DatePicker>
            </FormItem>
            <i-button type="success" icon="ios-search" class="lk-search-button" @click="search" shape="circle" :loading="loading"></i-button>
          </Form>
        </div>
        <div class="table-content">
          <el-table
            :data="list"
            style="width: 100%"
            class="text-center">
            <el-table-column
              type="index"
              label=""
              width="80"
              align="center">
            </el-table-column>
            <el-table-column
              prop="businessName"
              label=""
              show-overflow-tooltip
              align="center">
              <template slot-scope="scope">
                <span style="color: #CD853F">{{scope.row.businessName}}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="billTotal"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column
              prop="receivable"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column
              prop="put"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column
              prop="receipt"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column
              prop="residue"
              label=""
              show-overflow-tooltip
              align="center">
            </el-table-column>
            <el-table-column label="" align="center" width="200px" fixed="right">
              <template slot-scope="scope">
                <a @click="clickUpdate(scope.row, 2)"></a>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <hy-page v-show="pageShow"
                   :current-page="searchData.page"
                   :page-size="searchData.limit"
                   :total-element="totalElement"
                   @current-change="currentChange"></hy-page>
        </div>
      </TabPane>
    </Tabs>
    <Modal v-model="modalFormShow" :mask-closable="false">
      <p slot="header" class="lk-modal-title">
        <span></span>
      </p>
      <div style="text-align:left">
        <Form ref="form" :model="modalFormData" :rules="ruleValidate" :label-width="90">
          <hy-form-item :form="modalFormData" :formRow="modalFormItem"></hy-form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" :loading="modalBtnLoading" @click="addOrUpdateSubmit('form')"></Button>
        <Button type="ghost" style="margin-left: 8px" @click="modalFormShow = false"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import list from '@/js/mixins/list';
  import HyFormItem from '@/components/formItem/formItem.vue';
  import XLSX from 'xlsx';
  import saveAs from '@/js/libs/FileSaver';
  import { findOperatePageList, modifyFun, getSummary, modifySummary, initSummary } from '@/service/homeService/homeMService';
  export default {
    mixins: [list],
    components: {
      HyFormItem
    },
    data () {
      return {
        summaryList: [], // 
        summaryCodeList: [], // 
        summaryReferrerList: [], // 
        modalFormShow: false,
        modalBtnLoading: false,
        modalFormData: {},
        modalFormType: 1,
        modalFormItem: [
          {
            key: 'receipt',
            title: ''
          }
        ],
        ruleValidate: {
          receipts: [
            {required: true, message: '', trigger: 'blur change'}
          ]
        },
        searchClick: false,
        checkSearchData: {},
        searchDate: null,
        tabValue: 'one'
      };
    },
    async created () {
      try {
        await initSummary();
        let lowDate = new Date();
        lowDate.setDate(lowDate.getDate() + 1);
        this.searchDate = this._hyTool.DateFormat(new Date(lowDate), 'yyyy-MM-dd');
        this._getList();
      } catch (e) {
      }
    },
    methods: {
      async _getList () {
        try {
          this.loading = true;
          let para = {page: this.searchData.page, limit: this.searchData.limit};
          if (!this.searchDate) {
            let lowDate = new Date();
            lowDate.setDate(lowDate.getDate() + 1);
            this.searchDate = this._hyTool.DateFormat(new Date(lowDate), 'yyyy-MM-dd');
          }
          let payload = {
            startTime: this._hyTool.DateFormat(new Date(this.searchDate), 'yyyy-MM-dd'),
            endTime: this._hyTool.DateFormat(new Date(this.searchDate), 'yyyy-MM-dd')
          };
          if (this.searchClick) this.checkSearchData = Object.assign({}, this.searchData);
          let result = [];
          if (this.tabValue === 'one') {
            result = await getSummary(para);
            this.list = result.dataList || [];
            this.list.forEach(item => {
              let referrerReceiptSum = 0;
              let cardReceiptSum = 0;
              if (!item.cardAccountCollectDtos) item.cardAccountCollectDtos = [];
              if (!item.referrerAccountCollectDtos) item.referrerAccountCollectDtos = [];
              if (item.cardAccountCollectDtos && item.cardAccountCollectDtos.length) {
                item.cardAccountCollectDtos.forEach(it => {
                  cardReceiptSum = cardReceiptSum + it.cardReceiptSum;
                });
              }
              if (item.referrerAccountCollectDtos && item.referrerAccountCollectDtos.length) {
                item.referrerAccountCollectDtos.forEach(it => {
                  referrerReceiptSum = referrerReceiptSum + it.referrerReceiptSum;
                });
              }
              item.referrerReceiptSum = referrerReceiptSum;
              item.cardReceiptSum = cardReceiptSum;
            });
          } else {
            result = await findOperatePageList(para, payload);
            this.list = result.dataList || [];
          }

          this.loading = false;
          this.totalElement = result.totalCount;
          this.searchClick = false;
        } catch (e) {
          this.loading = false;
          this.list = [];
          this.totalElement = 0;
        }
      },
      clickUpdate (row, type) {
        this.modalFormType = type;
        this.modalFormData = Object.assign({}, row);
        if (type === 1) {
          this.modalFormItem = [
            {key: 'note', title: ''}
          ];
        } else {
          this.modalFormItem = [
            {key: 'receipt', title: ''}
          ];
          this.modalFormData.receipt = this.modalFormData.receipt.toString();
        }
        this.modalFormShow = true;
      },
      addOrUpdateSubmit (name) {
        this.$refs[name].validate(async (valid) => {
          try {
            if (valid) {
              this.modalBtnLoading = true;
              if (this.modalFormType === 1) {
                let payload = {
                  id: this.modalFormData.id,
                  note: this.modalFormData.note
                };
                await modifySummary(payload);
              } else {
                await modifyFun(this.modalFormData);
              }
              this.$Notice.success({title: ''});
              this.modalBtnLoading = false;
              this.modalFormShow = false;
              this.currentChange(1);
            } else {
              this.$Notice.warning({title: ''});
            }
          } catch (e) {
            this.modalBtnLoading = false;
          }
        });
      },
      clickTab (val) {
        this.tabValue = val;
        this.currentChange(1);
      },
      async importData () {
        let lowDate = new Date();
        lowDate.setDate(lowDate.getDate() + 1);
        let payload = {
          startTime: this._hyTool.DateFormat(new Date(), 'yyyy-MM-dd'),
          endTime: this._hyTool.DateFormat(new Date(lowDate), 'yyyy-MM-dd')
        };
        let result = await findOperatePageList({page: 1, limit: 200}, payload);
        let temp = result.dataList || [];
        let importData2 = [];
        temp.forEach(item => {
          importData2.push(
            {
              '': item.businessName,
              '': item.billTotal,
              '': item.receivable,
              '': item.put,
              '': item.receipt,
              '': item.residue
            }
          );
        });
        let importData1 = [];
        let result2 = await getSummary({page: 1, limit: 15});
        let temp2 = result2.dataList || [];
        temp2.forEach(item => {
          let referrerReceiptSum = 0;
          let cardReceiptSum = 0;
          if (!item.cardAccountCollectDtos) item.cardAccountCollectDtos = [];
          if (!item.referrerAccountCollectDtos) item.referrerAccountCollectDtos = [];
          if (item.cardAccountCollectDtos && item.cardAccountCollectDtos.length) {
            item.cardAccountCollectDtos.forEach(it => {
              cardReceiptSum = cardReceiptSum + it.cardReceiptSum;
            });
          }
          if (item.referrerAccountCollectDtos && item.referrerAccountCollectDtos.length) {
            item.referrerAccountCollectDtos.forEach(it => {
              referrerReceiptSum = referrerReceiptSum + it.referrerReceiptSum;
            });
          }
          item.referrerReceiptSum = referrerReceiptSum;
          item.cardReceiptSum = cardReceiptSum;
        });
        temp2.forEach(item => {
          importData1.push(
            {
              '': item.dateSummary,
              '': item.billSum,
              '': item.receivableSum,
              '': item.putSum,
              '': item.residue,
              '': item.residueLast,
              '': item.balance,
              '': item.note,
              '': item.cardReceiptSum,
              '': item.referrerReceiptSum
            }
          );
        });
        this.downloadExl(importData1, importData2);
      },
      downloadExl (data1, data2) {
        try {
          const wopts = {bookType: 'xlsx', bookSST: false, type: 'binary'}; // 
          const wb = { SheetNames: ['', ''], Sheets: {}, Props: {} };
          wb.Sheets[''] = XLSX.utils.json_to_sheet(data1);// json_to_sheet(Sheet)
          wb.Sheets['']['!cols'] = [{width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 20}];
          wb.Sheets[''] = XLSX.utils.json_to_sheet(data2);// json_to_sheet(Sheet)
          wb.Sheets['']['!cols'] = [{width: 30}, {width: 15}, {width: 15}, {width: 15}, {width: 15}, {width: 15}];
          saveAs(new Blob([this.s2ab(XLSX.write(wb, wopts))], {type: 'application/octet-stream'}), '' + '.' + (wopts.bookType === 'biff2' ? 'xls' : wopts.bookType));
          setTimeout(() => {
            this.$Spin.hide();
          }, 3000);
        } catch (e) {
          setTimeout(() => {
            this.$Spin.hide();
          }, 3000);
        }
      },
      s2ab (s) {
        if (typeof ArrayBuffer !== 'undefined') {
          let buf = new ArrayBuffer(s.length);
          var view = new Uint8Array(buf);
          for (let i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
          return buf;
        } else {
          let buf = new Array(s.length);
          for (let i = 0; i !== s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
          return buf;
        }
      }
    }
  };
</script>



